body {
  background: #40E0D0;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #FF0080, #FF8C00, #40E0D0);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #FF0080, #FF8C00, #40E0D0); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */  
  display: flex;
  justify-content: center;
}
.container {
  display: block;
  width: 500px;
  margin-top: 120px;
}
.title {
  text-align: center;
  color: #1A237E;
}
.new-task {
  display: block;
  width: 100%;
  background-color: azure;
  border-radius: 10px;
  height: 40px;
  border-color: aquamarine;
  border-width: 4px;
  text-align: center;
  font-size: 24px;
}

.test-label {
  text-align: center;
  color: #F50057;
}

.task-container {
  width: 100%;
  background-color: #CAC531;
  margin-bottom: 8px;
  font-size: 16px;
  border-left: 8px solid #2962FF !important;
}
.task {
  margin: 0;
  padding: 10px 0px 10px 10px;
}

.delete-button {
  background-color: #D50000;
  border-radius: 13px;
  height: 23px;
  color: black;
  border-width: 0px;
  font-weight: bolder;
}
.button-container {
  position: relative;
  float: right;
  margin: -32px 10px;
}